<!-- 联系我们页面 -->
<template>
  <div class="contact-page">
    <section class="py-5">
      <div class="container">
        <h2 class="contact-title">联系我们</h2>
        <div class="contact-info-box">
          <div class="contact-info-left">
            <div class="contact-info-main">中国大陆地址</div>
            <div class="contact-info-label">地址：四川省成都市高新区天府五街200号菁蓉汇7号楼</div>
            <div class="contact-info-label">电话：028-8888-8888</div>
            <div class="contact-info-label">邮箱：info@linfengai.com</div>
            <div class="contact-info-label">公司：四川麒风智能科技有限公司</div>
          </div>
          <div class="contact-info-right">
            <div class="contact-qrcode">联系二维码</div>
          </div>
        </div>
      </div>
    </section>
    <!-- 联系表单 -->
    <div class="contact-form-wrapper">
      <form class="contact-form" @submit.prevent="submitContactForm">
        <div class="contact-form-row">
          <input v-model="contactForm.name" type="text" class="contact-input" placeholder="你的姓名" required />
          <input v-model="contactForm.phone" type="text" class="contact-input" placeholder="联系方式" required />
        </div>
        <div class="contact-form-row">
          <input v-model="contactForm.email" type="email" class="contact-input" placeholder="联系邮箱" required />
        </div>
        <div class="contact-form-row">
          <textarea v-model="contactForm.message" class="contact-input contact-textarea" placeholder="你的需求" required></textarea>
        </div>
        <div class="contact-form-row contact-form-row-center">
          <button type="submit" class="contact-submit-btn">提交</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ContactView',
  data() {
    return {
      contactForm: {
        name: '',
        phone: '',
        email: '',
        message: ''
      }
    }
  },
  methods: {
    submitContactForm() {
      if (!this.contactForm.name || !this.contactForm.phone || !this.contactForm.email || !this.contactForm.message) {
        alert('请完整填写所有信息');
        return;
      }
      const subject = encodeURIComponent('网站联系表单留言');
      const body = encodeURIComponent(
        `姓名：${this.contactForm.name}\n联系方式：${this.contactForm.phone}\n邮箱：${this.contactForm.email}\n需求：${this.contactForm.message}`
      );
      window.location.href = `mailto:info@linfengai.com?subject=${subject}&body=${body}`;
    }
  }
}
</script>

<style scoped>
